.container {
    position:relative;
    width:100%;
    height:100%;
    font-size:2rem;

}
.rate-icon {
    position:absolute;
    color:white;
    background:#f8bb37;
    border-radius:50%;
    top: -6rem;
    left:-5rem;
    width:4rem;
    height:4rem;
    display:flex;
    justify-content: center;
    align-items: center;
}
.rate-icon span {
    font-size:2rem;
}

.actionBtns {
    position:absolute;
    right:-2rem;
    top:-3.5rem;
}
.actionBtns button {
    margin: 0 .5rem;
    border:none;
    background:#234234;
    color:white;
    font-size:1.4rem;
    border-radius:.3rem;
    transition: border 300ms;
    border:.3rem solid black;;
}

.actionBtns button:hover {
    border:.3rem solid #f8bb37;
}
